.header {
  font-size: 25px;
  font-weight: bolder;
  color: #2f00ff;
  padding-left: 3%;
  margin-top: 3%;
}

.submit-button {
  visibility: hidden;
}

.message-input-box {
  padding-left: 3%;
  padding-right: 3%;
}

.new-message {
  border: 2px solid #2f00ff;
}

.all-messages {
  height: 70vh;
  min-width: 400px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column-reverse;
  padding-left: 5%;
  margin-top: 2%;
}

.message {
  position: relative;
  text-align: bottom;
}

.name-input {
  padding-left: 3%;
  padding-right: 15%;
  padding-bottom: 3%;
}

.user-joined {
  color: #13cf8a
}
